const errors = require("./errors");
const {Method} = require("./method");
class Interface extends Method {
    constructor(options) {
        super(options);
    }

        
    buildCall(obj) {
        let self = this;
        return async (...args) => {
            this.validateArgs(args);
            if(typeof this.call != "function"){
                throw errors.IS_NOT_A_FUNC;
            }
            return await this.call.apply(obj, args);
        };
    }

}

module.exports = {
    Interface
};