class RpcEngine{
    constructor(opts){
        this._events = {};
        this._initEvent();
    }
    _initEvent(){
        global.addEventListener && global.addEventListener("message", (e) => {
            let data;
            try{
                data = JSON.parse(e.data);
            }catch(_){
                data = e.data;
            }
            this._onMessage(data);
        });
    }


    _onMessage(data){
        let { method, id } = data;
        if(!method || !id){
            return;
        }
        this.emit(`${method}:${id}`, data.error && new Error(data.error), data.data);
    }

    once(type, listener) {
        if (typeof listener !== 'function')
            throw new TypeError('"listener" argument must be a function');
          
        this._events[type] || (this._events[type] = []);
        this._events[type].push({
            once: true,
            fn: listener
        });
    };

    emit(type, ...args){
        let listeners = this._events[type];
        if(!listeners) return;
        for (let i = listeners.length - 1; i >= 0; i--) {
            let listener = listeners[i];
            listener.fn(...args);
            if(listener.once)
                listeners.splice(i, i);
        }
    }

    _sendMessage(data){
        global.DemosJS && global.DemosJS.postMessage(JSON.stringify(data));
    }

    sendAsync(data){
        let { method, id } = data;
        return new Promise((resolve, reject) => {
            this.once(`${method}:${id}`, (err, txid) => {
                if(err) return reject(err);
                resolve(txid);
            });
            this._sendMessage(data);
        });
    }
}


module.exports = {RpcEngine};